import React, { useState, useEffect } from "react";
import { Container, Row, Col } from "react-bootstrap";
import Button from "react-bootstrap/Button";
import Particle from "../Particle";
import Resumecontent from "./ResumeContent";
import axios from "axios";
import pdf from "../../Assets/akshay.pdf";
import { AiOutlineDownload } from "react-icons/ai";

function Resume() {
  const uri = "https://porfolio-backend.vercel.app/ranks/getRanks";
  const [spojRank, upadteSpojRank] = useState(0);
  const [hackerrank, upadteHackerank] = useState(0);
  const [sem, upadateSem] = useState(0);
  const [cgpa, upadteCgpa] = useState(0);

  useEffect(() => {
    axios
      .get(uri)
      .then((res) => {
        upadteSpojRank(res.data.message[0].spojRank);
        upadteHackerank(res.data.message[1].hackerrank);
        upadteCgpa(res.data.message[2].cgpa);
        upadateSem(res.data.message[3].sem);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);

  return (
    <Container fluid className="resume-section">
      <Particle />
      <Container>
        <Row style={{ justifyContent: "center", position: "relative" }}>
          <Button variant="primary" href={pdf} target="_blank">
            <AiOutlineDownload />
            &nbsp;Download CV
          </Button>
        </Row>
        <Row className="resume">
          <Col md={6} className="resume-left">
            <h3 className="resume-title">INTERNSHIP</h3>
            <Resumecontent
              title="Student"
          
              content={[
                "Cloud Computing, IOT, Software Testing in Veriton Soware (2018)",
                "Phthon Programming in Veriton Software (2021).",
              
              ]}
            />
            <h3 className="resume-title">Extracurricular Activities</h3>
            <Resumecontent
              title="Photography"
              content={[
                "What I like about photographs is that they capture a moment that's gone forever, impossible to reproduce",
              ]}
            />
          </Col>
          <Col md={6} className="resume-right">
            <h3 className="resume-title">Education</h3>
            <Resumecontent
              title="SSLC [Savithri Convent, Mysore] "
              date="2016"
              content={[`Percentage: 85.12% `]}
            />
            <Resumecontent
              title="Diploma In Computer Science [Vidyavardhaka Polytechnic, Mysore] "
              date="2016-2019"
              content={[`Percentage: 71.25% `]}
            />
            <Resumecontent
              title="Computer Science and Engineering [PES College Engineering, Mandya] "
              date="2019-present"
              content={[`CGPA: 7.84% `]}
            />
            

      

            <h3 className="resume-title">Achivements</h3>
            <Resumecontent
              title=""
              content={[
                `RUNNER UP IN,
                A STATE LEVEL PROJECT EXHIBITION FOR POLYTECHNIC STUDENTS Held in
                COORG INSTITUTE OF TECHNOLOGY (2019)`,
                
              ]}
            />
          </Col>
        </Row>
        <Row style={{ justifyContent: "center", position: "relative" }}>
          <Button variant="primary" href={pdf} target="_blank">
            <AiOutlineDownload />
            &nbsp;Download CV
          </Button>
        </Row>
      </Container>
    </Container>
  );
}

export default Resume;
